*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  margin: 0;
  padding: 0;
}

html {
  box-sizing: border-box;
  font: 100% sans-serif;
}

html,
body {
  height: 100%;
}

img,
picture, video,
canvas, svg {
  display: block;
  max-width: 100%;
}

input, button,
textarea, select {
  font: inherit;
}

p,
h1, h2,
h3, h4,
h5, h6 {
  overflow-wrap: break-word;
}

a,
a:link,
a:visited,
a:hover,
a:active {
  border: 2px solid #000;
  border-radius: 5px;
  padding: 5px;
  background-color: #aaa;
  color: #000;
  text-decoration: none;
}

/* === */

#root {
  height: 100%;
}